<template>
	<div class="Geren">
		<div class="header">
			<div>
				<van-icon name="arrow-left" @click="tui"/>
			</div>
			<div>账户信息</div>
		</div>
		<div class="content">
			<div class="kb"></div>
			<div class="tx" @click.stop="uploadHeadImg">
				<div>头像</div>
				<div>
					<div>
						<!--accept=”image/*”表示不限制格式。-->
						<img :src="img1" class="tp"/>
						<input type="file" accept="image/*" class="hiddenInput" @change="handleFile">
					</div>
					<div>
						<van-icon name="arrow"/>
					</div>
				</div>
			</div>
			<div class="yh">
				<div>用户名</div>
				<div>
					{{this.$store.state.login.name}}
					<van-icon name="arrow"/>
				</div>
			</div>
			<router-link to="Add" tag="div">
				<div class="sh">
					<div>收货地址</div>
					<div>
						<van-icon name="arrow"/>
					</div>
				</div>
			</router-link>
			<div class="zh">
				<div>账号绑定</div>
			</div>
			<div class="sj">
				<div>手机</div>
				<div>
					<van-icon name="arrow"/>
				</div>
			</div>
			<div class="aq">
				<div>安全设置</div>
			</div>
			<div class="dl">
				<div>登录密码</div>
				<div>
					修改
					<van-icon name="arrow"/>
				</div>
			</div>
			<div class="tc">
				<input type="button" value="退出登录">
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		components: {},
		name: "Geren",
		data() {
			return {
				img1: 'https://qlogo1.store.qq.com/qzone/852165136/852165136/100?1566388361'
			}
		},
		methods: {

			uploadHeadImg: function () {
				this.$el.querySelector('.hiddenInput').click()
			},
			tui() {
				this.$router.go(-1);
			},
			handleFile(e) {
				let $target = e.target || e.srcElement
				let file = $target.files[0]
				var reader = new FileReader()
				reader.onload = (data) => {
					let res = data.target || data.srcElement
					this.img1 = res.result
					localStorage.img1 = this.img1
				}
				reader.readAsDataURL(file)
			}
		},
		mounted() {
			if (localStorage.img1) {
				this.img1 = localStorage.img1

			}
		}
	}
</script>

<style lang="scss" scoped>
	.Geren {
		width: 100%;
		height: 100%;
	}

	.header {
		width: 100%;
		height: 1.5rem;
		background: #3190E8;
		display: flex;
		align-items: center;
		div {
			font-size: 0.5rem;
			color: white;
		}
		div:nth-of-type(1) {
			margin-left: 0.3rem;
			margin-top: 0.15rem;
		}
		div:nth-of-type(2) {
			margin-left: 3.5rem;
		}
	}

	.content {
		.kb {
			width: 100%;
			height: 0.3rem;
			background: #F5F5F5;
		}
		.tx {
			width: 100%;
			height: 1.5rem;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid #CCCCCC;
			> div:nth-of-type(1) {
				margin-left: 0.5rem;
			}
			div:nth-of-type(2) {
				display: flex;
				align-items: center;
				div:nth-of-type(1) {
					margin-right: 0.3rem;
				}
				div:nth-of-type(2) {
					margin-right: 0.5rem;
				}
			}
			.tp {
				width: 1.4rem;
				height: 1.4rem;
				border-radius: 50%;
			}
		}
		.hiddenInput {
			display: none;
			/*opacity: 0;*/
		}
		.yh {
			width: 100%;
			height: 1rem;
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 1px solid #CCCCCC;
			div:nth-of-type(1) {
				margin-left: 0.5rem;
			}
			div:nth-of-type(2) {
				margin-right: 0.5rem;
			}
		}
		.sh {
			@extend .yh;
		}
		.zh {
			width: 100%;
			height: 0.8rem;
			line-height: 0.8rem;
			background: #F5F5F5;
			div {
				margin-left: 0.5rem;
				color: #808080;

			}
		}
		.sj {
			@extend .yh;
		}
		.aq {
			@extend .zh;
		}
		.dl {
			@extend .yh;
		}
		.tc {
			width: 100%;
			height: 3rem;
			text-align: center;
			input {
				width: 80%;
				height: 1rem;
				background: #D8584A;
				border: 1px solid red;
				border: none;
				color: white;
				border-radius: 5px;
				margin-top: 1rem;
			}
		}
	}
</style>



